

















import uniLoadMore from '../../../components/uni-load-more.vue'

export default {
	data() {
		return {
			title: 'uni-load-more',
			list: [],
			loadingType: 0,
			contentText: {
				contentdown: "上拉显示更多",
				contentrefresh: "正在加载...",
				contentnomore: "没有更多数据了"
			}
		}
	},
	onLoad() {
		let list = [];
		for (let i = 1; i < 11; i++) {
			list.push(i);
		}
		this.list = list;
	},
	onReachBottom() {
		if (this.loadingType !== 0) {
			return;
		}
		this.loadingType = 1;
		let list = [],
			maxItem = this.list[this.list.length - 1],
			length = maxItem + 6;
		for (let i = maxItem + 1; i < length; i++) {
			list.push(i);
		}
		setTimeout(() => {
			if (length > 26) {
				this.loadingType = 2;
				return;
			}
			this.list = this.list.concat(list);
			this.loadingType = 0;
		}, 800);
	},
	components: {
		uniLoadMore
	}
}
